___CSS_LOADER_EXPORT___.push([module.id, `:root {
    --background-color: #0A2463;
    --ocean-color: #3E92CC;
    --ship-color: #8B8C89;
    --hit-color: #D8315B;
    --miss-color: #FFFAFF;
}

@font-face {
    font-family: 'ITC Machine Bold';
    src: url(${___CSS_LOADER_URL_REPLACEMENT_0___}) format('truetype');
}

* {
    padding: 0px;
    margin: 0px;
    font-family: "ITC Machine Bold", system-ui;
    letter-spacing: 2px;
}

body {
    background-color: var(--background-color);
    color: white;
}

.start-button {
    font-family: 'ITC Machine Bold';
    color: white;
    background-color: black;
}


#container {
    display: grid;
    grid-template: 2fr 8fr 2fr / 1fr ;
    height: 100vh;
    width: 100vw;
}

#header {
    display: flex;
    justify-content: center;
    align-items: center;
    grid-row: 1 / 2;
    margin: 18px;
}

#header img {
    min-width: 300px;
    max-height: 200px;
}

#game-area {
    display: grid;
    grid-template: 100px auto / 1fr 1fr;
    grid-row: 2 / 3;
    padding: 0px 50px;
    column-gap: 100px;
    color: white;
    position: relative;
}

#game-area > div {
    display: none;
    grid-row: 2 / 3;
    grid-template: repeat(10, minmax(50px, 1fr)) / repeat(10, minmax(50px, 1fr));
    border: 2px solid black;
}

#game-area > p {
    display: none;
    justify-content: center;
    align-items: center;
    font-size: 4rem;
}

#game-area > .start-button {
    display: flex;
    height: 200px;
    width: 300px;
    font-size: 4rem;
    align-self: center;
    justify-self: center;
    justify-content: center;
    align-items: center;
    grid-row: 1 / 3;
    grid-column: 1 / 3;
    border: none;
    background-color: inherit;
}

.grid-cell {
    border: 1px solid black;
    background-color: var(--ocean-color);
}

.grid-cell:hover {
    background-color: rgba(128, 128, 128, 0.1)
}

#footer {
    display: flex;
    gap: 20px;
    font-size: 2rem;
    justify-content:center;
    align-items: center;
    padding: 20px;
    color: white;
}

.winner-message {
    display: flex;
    justify-content: end;
    align-items: center;
    min-height: 2rem;
    min-width: 2rem;
}

.play-again-button {
    min-height: 2rem;
    min-width: 2rem;
    background-size: cover;
    border: none;
    background-color: inherit;
}`, "",{"version":3,"sources":["webpack://./src/style.css"],"names":[],"mappings":"AAAA;IACI,2BAA2B;IAC3B,sBAAsB;IACtB,qBAAqB;IACrB,oBAAoB;IACpB,qBAAqB;AACzB;;AAEA;IACI,+BAA+B;IAC/B,+DAA2D;AAC/D;;AAEA;IACI,YAAY;IACZ,WAAW;IACX,0CAA0C;IAC1C,mBAAmB;AACvB;;AAEA;IACI,yCAAyC;IACzC,YAAY;AAChB;;AAEA;IACI,+BAA+B;IAC/B,YAAY;IACZ,uBAAuB;AAC3B;;;AAGA;IACI,aAAa;IACb,iCAAiC;IACjC,aAAa;IACb,YAAY;AAChB;;AAEA;IACI,aAAa;IACb,uBAAuB;IACvB,mBAAmB;IACnB,eAAe;IACf,YAAY;AAChB;;AAEA;IACI,gBAAgB;IAChB,iBAAiB;AACrB;;AAEA;IACI,aAAa;IACb,mCAAmC;IACnC,eAAe;IACf,iBAAiB;IACjB,iBAAiB;IACjB,YAAY;IACZ,kBAAkB;AACtB;;AAEA;IACI,aAAa;IACb,eAAe;IACf,4EAA4E;IAC5E,uBAAuB;AAC3B;;AAEA;IACI,aAAa;IACb,uBAAuB;IACvB,mBAAmB;IACnB,eAAe;AACnB;;AAEA;IACI,aAAa;IACb,aAAa;IACb,YAAY;IACZ,eAAe;IACf,kBAAkB;IAClB,oBAAoB;IACpB,uBAAuB;IACvB,mBAAmB;IACnB,eAAe;IACf,kBAAkB;IAClB,YAAY;IACZ,yBAAyB;AAC7B;;AAEA;IACI,uBAAuB;IACvB,oCAAoC;AACxC;;AAEA;IACI;AACJ;;AAEA;IACI,aAAa;IACb,SAAS;IACT,eAAe;IACf,sBAAsB;IACtB,mBAAmB;IACnB,aAAa;IACb,YAAY;AAChB;;AAEA;IACI,aAAa;IACb,oBAAoB;IACpB,mBAAmB;IACnB,gBAAgB;IAChB,eAAe;AACnB;;AAEA;IACI,gBAAgB;IAChB,eAAe;IACf,sBAAsB;IACtB,YAAY;IACZ,yBAAyB;AAC7B","sourcesContent":[":root {\n    --background-color: #0A2463;\n    --ocean-color: #3E92CC;\n    --ship-color: #8B8C89;\n    --hit-color: #D8315B;\n    --miss-color: #FFFAFF;\n}\n\n@font-face {\n    font-family: 'ITC Machine Bold';\n    src: url('./fonts/ITC Machine Bold.ttf') format('truetype');\n}\n\n* {\n    padding: 0px;\n    margin: 0px;\n    font-family: \"ITC Machine Bold\", system-ui;\n    letter-spacing: 2px;\n}\n\nbody {\n    background-color: var(--background-color);\n    color: white;\n}\n\n.start-button {\n    font-family: 'ITC Machine Bold';\n    color: white;\n    background-color: black;\n}\n\n\n#container {\n    display: grid;\n    grid-template: 2fr 8fr 2fr / 1fr ;\n    height: 100vh;\n    width: 100vw;\n}\n\n#header {\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    grid-row: 1 / 2;\n    margin: 18px;\n}\